*{
  box-sizing: border-box;
  /* outline: 1px solid red; */
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: #099db7;
}

.nav-links {
  display: flex;
  justify-content: center;
  text-align: center;
  list-style: none;
  grid-gap: 20px;
  gap: 20px;
}

li {
  color: #ffffff;
  font-size: 20px;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-title {
  color: #ffffff;
}

li:hover {
  transition: all 1s 0s ease;
  background-color: black;
}
.shop-button{
  background: #3498db;
  background-image: linear-gradient(to bottom, #3498db, #2980b9);
  font-family: Arial;
  border: 0;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.shop-button:hover{
  background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

img {
  width: 100%;
}

.button-container {
  position: absolute;
  top: 60%;
  left: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.item-container {
    display: grid;
    grid-template-columns: repeat(3, 300px);
    justify-content: center;
    align-items: center;
    grid-gap: 50px;
    gap: 50px;
    margin-top: 10px;
}

.shopping-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 300px;
    background-color: rgba(190, 187, 187, 0.144); 
    color: #3498db;
}

.image-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.price-container {
    display: flex;
    justify-content: center;
}

.shop-images {
    width: 150px;
    height: 150px;
}

.add-to-cart-button {
    background: #3498db;
    background-image: linear-gradient(to bottom, #3498db, #2980b9);
    font-family: Arial;
    color: #ffffff;
    font-size: 20px;
    text-decoration: none;
    border: 0;
}

.add-to-cart-button:hover {
    background: #3cb0fd;
    background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
    text-decoration: none;
}
.cart-container {
    display: flex;
    flex-direction: column;
    grid-gap: 50px;
    gap: 50px;
    margin-top: 60px;
    margin: 20px;
}

.price-quantity-total-container {
    display: flex;
    justify-content: flex-end;
    grid-gap: 85px;
    gap: 85px;
    margin: 20px;
}

.eachItem-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.eachItem-container-item1 {
    display: flex;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;

}

.eachItem-container-item2 {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.eachItem-container-item3 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    grid-gap: 50px;
    gap: 50px;
}

.cart-image {
    width: 150px;
    height: 150px;
}

.eachItem-submission-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    grid-gap: 40px;
    gap: 40px;
    align-items: center;
    margin: 20px;
}

.eachItem-submission {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.quantity-container {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.handle-button{
    background: #3498db;
    background-image: linear-gradient(to bottom, #3498db, #2980b9);
    font-family: Arial;
    color: #ffffff;
    font-size: 20px;
    text-decoration: none;
    border: 0;
}

.handle-button:hover{
    background: #3cb0fd;
    background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
    text-decoration: none;
  }
.contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
